<template>
  <div class="modify-password">
    <HeaderComponent pageName="register"></HeaderComponent>
    <el-alert
      class="tip"
      title="重设密码"
      description="您的密码过于简单。密码设置要求8~16位，包含大写字母、小写字母、数字、特殊字符（.!@#$%^&*`~()-+=），任意3种组合；"
      type="warning"
    />
    <div class="container">
      <h6>修改密码</h6>
      <el-form ref="formRef" :model="formData" :rules="rules">
        <el-form-item prop="oldPassword" class="password-item">
          <el-input
            v-model.trim="formData.oldPassword"
            :type="paramsData.oldPasswordType"
            placeholder="请输入旧密码"
          ></el-input>
          <svg
            class="icon"
            aria-hidden="true"
            @click="handlePasswordType('oldPasswordType', paramsData)"
          >
            <use
              :xlink:href="
                paramsData.oldPasswordType === 'password'
                  ? '#icon-password-hide'
                  : '#icon-password-show'
              "
            ></use>
          </svg>
        </el-form-item>
        <el-form-item prop="password" class="password-item">
          <el-input
            v-model.trim="formData.password"
            :type="paramsData.passwordType"
            placeholder="请输入新密码"
          ></el-input>
          <svg
            class="icon"
            aria-hidden="true"
            @click="handlePasswordType('passwordType', paramsData)"
          >
            <use
              :xlink:href="
                paramsData.passwordType === 'password'
                  ? '#icon-password-hide'
                  : '#icon-password-show'
              "
            ></use>
          </svg>
        </el-form-item>
        <el-form-item prop="rePassword" class="password-item">
          <el-input
            v-model.trim="formData.rePassword"
            :type="paramsData.rePasswordType"
            placeholder="请再次输入确认密码"
          ></el-input>
          <svg
            class="icon"
            aria-hidden="true"
            @click="handlePasswordType('rePasswordType', paramsData)"
          >
            <use
              :xlink:href="
                paramsData.rePasswordType === 'password'
                  ? '#icon-password-hide'
                  : '#icon-password-show'
              "
            ></use>
          </svg>
        </el-form-item>
        <el-form-item>
          <div class="submit-button" @click="submit">登录</div>
        </el-form-item>
      </el-form>
    </div>
    <div class="copyright">{{ storeState.copyright }}</div>
  </div>
</template>

<script lang="ts" setup>
import { ElMessage } from "element-plus";
import { ref, reactive } from "vue";

import { LocalStorage } from "jz-tool-lib";
import { useVueRouter, useVueStore } from "@assets/useHook";
import { validatePassword, validateRePassword } from "@assets/validatorHook";

import { modifyPasswordApi } from "@api/login-register-api";
import HeaderComponent from "@components/login-regietr/header-component.vue";

defineOptions({
  name: "forgetPassword",
});

const formRef = ref();
const { goPage } = useVueRouter();
const { storeState } = useVueStore();
const authorization = LocalStorage.getItem("bd_authorization");
const paramsData = reactive({
  submitFlag: false,
  oldPasswordType: "password",
  passwordType: "password",
  rePasswordType: "password",
});
const formData = reactive({
  oldPassword: "",
  password: "",
  rePassword: "",
});
const rules = {
  oldPassword: [
    {
      required: true,
      message: "请输入旧密码",
      trigger: "blur",
    },
  ],
  password: [
    {
      validator: (rule, value, callback) => {
        validatePassword(
          rule,
          value,
          callback,
          formData,
          formRef,
          "rePassword"
        );
      },
      trigger: "blur",
    },
  ],
  rePassword: [
    {
      validator: (rule, value, callback) => {
        validateRePassword(rule, value, callback, formData, "password");
      },
      trigger: "blur",
    },
  ],
};
const handlePasswordType = (type, data) => {
  data[type] = data[type] === "text" ? "password" : "text";
};

const submit = () => {
  formRef.value.validate((valid: boolean) => {
    if (valid) {
      if (paramsData.submitFlag) return;
      paramsData.submitFlag = true;
      let params = {
        oldPwd: formData.oldPassword,
        newPwd: formData.password,
        token: authorization,
      };
      modifyPasswordApi
        .getData(params)
        .then(() => {
          paramsData.submitFlag = false;
          ElMessage({
            message: "密码修改成功,请重新登录",
            type: "success",
          });
          goPage("/login");
        })
        .catch(() => {
          paramsData.submitFlag = false;
        });
    } else {
      return false;
    }
  });
};
</script>
<style lang="scss" scoped>
.modify-password {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 100%;
  min-height: 100%;
  background: #f5f7fb;
  :deep(.tip.el-alert) {
    position: absolute;
    top: 100px;
    width: 438px;
    z-index: 9;
    .el-alert__content {
      padding: 0 10px;
    }
    .el-alert__title {
      font-size: 15px;
    }
    .el-alert__description {
      font-size: 13px;
      line-height: 20px;
    }
  }
  .container {
    width: 520px;
    padding: 48px 48px 40px;
    z-index: 1;
    background: #ffffff;
    box-shadow: 0 10px 50px 0 rgba(100, 107, 122, 0.1);
    border-radius: 8px;

    h6 {
      font-size: 28px;
      font-weight: 600;
      color: #242934;
      line-height: 28px;
      text-align: center;
      margin-bottom: 58px;
    }
    :deep(.el-form) {
      .el-form-item {
        margin-bottom: 32px;
        .el-input__wrapper {
          height: 58px;
          padding: 15px 24px;
          border-radius: 4px;
          background: #f5f7fb;
          box-shadow: none;
          &.is-focus {
            border: 1px solid #0056ff;
          }
        }
        .el-input__inner {
          font-size: 20px;
          font-weight: 600;
          color: #242934;

          &::placeholder {
            font-size: 16px;
            font-weight: 400;
            color: #9ca6b9;
          }
        }

        &.is-error {
          .el-input__wrapper {
            background: rgba(241, 37, 37, 0.05);

            &::placeholder {
              color: #f12525;
            }
          }
        }
      }

      .password-item {
        .el-form-item__content {
          position: relative;
        }
        .el-input__wrapper {
          padding-right: 56px;
        }

        .icon {
          position: absolute;
          top: 17px;
          right: 16px;
          width: 24px;
          height: 24px;
          cursor: pointer;
        }

        .prompt {
          height: 32px;
          margin-top: 10px;
          line-height: 16px;
          color: #f12525;
        }
      }

      .submit-button {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 60px;
        cursor: pointer;
        font-size: 18px;
        color: #ffffff;
        line-height: 25px;
        background: #0056ff;
        border-radius: 4px;
      }
    }
  }

  .copyright {
    position: absolute;
    bottom: 80px;
    width: 100%;
    font-size: 14px;
    color: #9ca6b9;
    line-height: 1;
    text-align: center;
  }
}
</style>
